		return {
			List:['已报名','我喜欢'],
			scrollLeft: 0,
			TabCur :0,
			subList:['进行中','即将开始','已结束'],
			subTabCur:0,
			promotiondata:[{
				'dataid':'1',
				'name':'单人乒乓球比赛',
				'status':'进行中',
				'operate' :'查看',
				'code':'1' , //1,进行中 2.即将开始 3.已结束
			},{
				'dataid':'2',
